import {
	mapState,
	mapActions,
	mapMutations
} from 'vuex'
import addressmodel from '../../components/address/addressmodel.vue'
export default {
	name: "",
	components: {
		addressmodel
	},
	props: {},
	data() {
		return {
			// 接收openId
			openId: '',
			// 如果是跳转过来选择地址参数
			buyAddress: false
		}
	},
	methods: {
		...mapActions(['getAllAddresses']),
		...mapMutations(['setAddressObj']),
		// 没有地址列表时点击去添加
		goToAdd() {
			uni.navigateTo({
				url: '../addressedit/addressedit',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 选择地址
		chooseAddress(item) {
			if (this.buyAddress) {
				this.setAddressObj(item)
				uni.navigateBack({
					delta: 1
				});
			}
		}
	},
	mounted() {
		if (uni.getStorageSync('openId')) {
			this.openId = uni.getStorageSync('openId')
		}
		if (this.openId !== '') {
			this.getAllAddresses(this.openId)
		}
	},
	onShow() {
		if (this.openId !== '') {
			this.getAllAddresses(this.openId)
		}
	},
	onLoad(option) {
		if (option.buyAddress) {
			this.buyAddress = option.buyAddress
		}
	},
	filters: {

	},
	computed: {
		...mapState(['addressArr']),
	},
	watch: {

	},
	directives: {

	}
}
